.page {
  font-family: Roboto, sans-serif;
  scroll-behavior: smooth;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: $color-text-grey;

  &__body {
    // background-color: #f2f2f2; /* temp */
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }
  }

  &__section-title {
    margin: 0 0 32px;

    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;
    text-align: center;
    color: #000;

    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }

    &::after {
      content: "";
      display: block;
      width: 23px;
      height: 16px;

      background-image: url(../images/crown.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      margin: 4px auto 0;
    }
  }
}
